import { createHashRouter } from "react-router-dom";
import App from "./App";
import DetailMovie from "./routes/DetailMovie";
import Home from "./routes/Home";
import Search from "./routes/Search";
import Tv from "./routes/Tv";
import DetailTv from "./routes/DetailTv";
import DetailPerson from "./routes/DetailPerson";
import Trending from "./routes/Trending";

/**
 * ghpage   react-router-dom createHashRouter 
 * createHashRouter  root url # 
 *  createBrowserRouter  
 */
const router = createHashRouter([
    {
        path: `/`,
        element: <App />,
        children: [
            {
                path: "",
                element: <Home />,
                children: [
                    {
                        path: "movie/:keyName/:id",
                        element: <DetailMovie />,
                    },
                ],
            },{
                path: "tv",
                element: <Tv />,
                children: [
                    {
                        path: ":keyName/:id",
                        element: <DetailTv />,
                    },
                ],
            },{
                path: "trending",
                element: <Trending />,
                children: [
                    {
                        path: "movie/:id",
                        element: <DetailMovie />,
                    },{
                        path: "tv/:id",
                        element: <DetailTv />,
                    },{
                        path: "person/:id",
                        element: <DetailPerson />
                    },
                ],
            },{
                path: "search",
                element: <Search />,
                children: [
                    {
                        path: "movie/:id",
                        element: <DetailMovie />,
                    },{
                        path: "tv/:id",
                        element: <DetailTv />,
                    },{
                        path: "person/:id",
                        element: <DetailPerson />
                    },
                ],
            },
        ],
    }
]);


export default router;